.navbar{
    background: rgb(30,30,30);
    height:5rem;
    /* width: 100vw; */
    /* max-width: 75rem; */
    margin: 0 ;
    padding: 1rem 2rem;
    display: flex;
    justify-content: space-between;
    position:sticky;
    top:0;
    z-index: 3;
}
.logo{
    object-fit: cover;
    height: 5rem;
    width: 4rem;


}
.desktopMenu{
    margin-top: 2rem;
}
.desktopMenuListItem{
    
    margin: 2rem;
    cursor: pointer;

}
.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
    

}
.desktopMenuBtn{
    background: white;
    margin-top: 2rem;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:1rem 1rem ;
    border-radius: 2rem;

}
.desktopMenuBtn:hover{
    background: rgb(243, 243, 162);
    
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}
.active{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.menuImg{
    display: none;
    object-fit: cover;
    height:1.8rem ;
}
 
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40, 40);

}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
}

@media screen and (max-width:720px) {
    .menuImg{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
    
}
